.App {
  text-align: center;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.header {
  overflow: hidden;
  background-color: #f1f1f1;
  padding: 2vh;
  position: sticky;
  top: 0;
  z-index: 5;
}

.header a {
  float: left;
  color: deeppink;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 2em; 
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-size: 25px;
  font-weight: bold;
}

.header a:hover {
  background-color: #ddd;
  color: black;
}

.header a.active {
  background-color: dodgerblue;
  color: white;
}
.header-right {
    float: right;
  }