.chat-body{
    height: 85vh;
    margin: auto;
    position: relative;
    width: 70vw;
}

.chat-message-container{
    height: 70vh;
    margin-bottom: 5vh;
    overflow: auto;
    padding: 10px;
}
.chat-inputs{
    height: 10vh;
    margin-bottom: 2vh;
    position: fixed;
    text-align: center;
    bottom: 5vh;
}

.input-text{
    height: 25px;
    border: 2px solid gray;
    width: 55vw;
    border-radius: 15px;
    padding: 5px;
}

.input-button{
    height: 39px;
    border: 2px solid #2e6da4;
    width: 10vw;
    margin: 10px;
    background-color: #337ab7;
    color: honeydew;
    border-radius: 15px;
}

.message.right {
    width: 100%;
    text-align: right;
    background-color: lightyellow;
    border-radius: 5px;
    padding: 5px;
}

.message.left {
    width: 100%;
    text-align: left;
    background-color: lightskyblue;
    border-radius: 5px;
    padding: 5px;
}

